<template>
    <div class="procedure_approval_condition">
        <div class="c_approval_condition_header">
            <CContentHeader :isShowIcon="true" content="条件审批流程" />

            <el-button v-if="typeof this.ruleListIndex == 'number'" class="c_set_up_two_header_btn_one" @click="reviseBtn" type="primary">修改</el-button>
            <el-button v-else class="c_set_up_two_header_btn_one" @click="preserveBtn" type="primary">保存</el-button>
            <el-button class="c_set_up_two_header_btn_two" @click="returnBtn" type="primary">取消</el-button>
        </div>
        <div class="c_approval_condition_box">
            <div class="c_approval_condition_main">
                <div class="c_approval_condition_form">
                    <div class="c_approval_condition_input_box">
                        <div class="c_title">
                            流程名称：
                        </div>
                        <div class="c_input c_input_color">
                            <el-input v-model="processName" placeholder="请输入流程名称"></el-input>
                        </div>
                    </div>
                    <div class="c_approval_condition_input_box">
                        <div class="c_title">
                            同时满足:
                        </div>
                        <div class="c_select">
                            <el-select v-model="region">
                                <el-option label="申请人" value="申请人"></el-option>
                                <!-- <el-option label="抄送人" value="抄送人"></el-option> -->
                            </el-select>
                            <span v-if="rules.length > 0">为</span>
                            <div class="c_name" v-for="item in rules" :key="item.depid">
                                <img v-if="item.type === 'dep'" src="../../assets/img/procedure/部门白.png" alt="">
                                <img v-else src="../../assets/img/procedure/人白.png" alt="">
                                {{item.name}}
                            </div>
                            <!-- <div class="c_name">
                                <img src="../../assets/img/procedure/人白.png" alt="">
                                梁俊伟
                            </div> -->
                            <div class="c_btn" @click="reviseRule" v-if="rules.length > 0">
                                修改
                            </div>
                            <div class="c_btn ml" @click="reviseRule" v-else>
                                添加
                            </div>
                        </div>
                    </div>
                </div>
                <div class="c_approval_condition_add">
                    <div class="c_approval_condition_title">
                        审批流程
                    </div>
                    <div class="c_approval_process_box">
                        <div class="c_title">
                            审批人：
                        </div>
                        <div class="c_approval_by_list">
                            <div class="flex" v-for="(item,index) in flows" :key="index">
                                <!-- 头像盒子 -->
                                <div class="c_approval_by_name">
                                    <!-- <img :src="require('../../assets/img/procedure/组56.png')" alt=""> -->
                                    <img v-if="item.avatar" :src="item.avatar" alt="">
                                    <img v-else :src="require('../../assets/img/procedure/staff.png')" alt="">
                                    <p>{{item.name}}</p>
                                </div>
                                <!-- 三点图标 -->
                                <div class="c_approval_by_icon">
                                    <i class="el-icon-more"></i>
                                </div>
                            </div>
                            <!-- 添加图标 -->
                            <div class="c_approval_by_add" @click="reviseFlow">
                                <i class="el-icon-plus"></i>
                            </div>
                        </div>
                    </div>
                    <div class="c_approval_process_box">
                        <div class="c_title">
                            抄送人：
                        </div>
                        <div class="c_approval_by_list">
                            <div class="flex" v-for="(item,index) in copys" :key="index">
                                <!-- 头像盒子 -->
                                <div class="c_approval_by_name">
                                    <!-- <img :src="require('../../assets/img/procedure/组56.png')" alt=""> -->
                                    <img v-if="item.avatar" :src="item.avatar" alt="">
                                    <img v-else :src="require('../../assets/img/procedure/staff.png')" alt="">
                                    <p>{{item.name}}</p>
                                </div>
                                <!-- 三点图标 -->
                                <div class="c_approval_by_icon">
                                    <i class="el-icon-more"></i>
                                </div>
                            </div>
                            <!-- 添加图标 -->
                            <div class="c_approval_by_add" @click="reviseCopy">
                                <i class="el-icon-plus"></i>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!-- 遮罩层组件 -->
        <div class="c_elastic_frame">
            <!-- <MaskLayer title="可见范围修改" :menuList="menuList" :dialogShow.sync="dialogShow" @cancel="cancelBtn"></MaskLayer> -->
            <!-- <MaskLayer v-if="dialogShow" :title="title" :twoTitle="twoTitle" :dialogShow.sync="dialogShow" :isOne="isOne" :isUser="isUser" :powerList="powerList" @submit="submitBtn" @reset="resetBtn" /> -->
            <MaskLayer v-if="dialogShow" :title="title" :twoTitle="twoTitle" :isOne="isOne" :isUser="isUser" :dialogShow.sync="dialogShow" :powerList="powerList" @submit="submitBtn" @reset="resetBtn"></MaskLayer>
        </div>
    </div>
</template>

<script>
    import MaskLayer from '../../components/c_maskLayer.vue'

    export default {
        data() {
            return {
                region: '申请人',
                title: '申请人',   //权限选择标题
                twoTitle: '已选择的部门或成员',  //权限选择副标题
                m_id: '',
                processName: '',
                isUser: false, //是否只选择人
                dialogShow: false, //是否显示遮罩层 
                powerList: [], //已选权限数组
                isRule: false, //是否修改可见范围
                isFlow: false, //是否修改审批人
                isCopy: false, //是否修改抄送人 
                isOne: false,

                rules: [], //修改按钮修改的数组
                flows: [], //审批人数组
                copys: [], //抄送人数组
                ruleList: [],
                ruleListIndex: -1,
                ruleSettingsData: {},

                isDel: true
            }
        },
        mounted() {
            // console.log(this.$route.query.m_id);
            // this.m_id = this.$route.query.m_id;


            //获取本地存储的ruleList。   如果有ruleList说明是修改流程规则，没有则是添加流程规则
            this.ruleSettingsData = this.$zero.getSessionStorage('ruleSettingsData');
            this.ruleListIndex = this.ruleSettingsData.index;
            console.log('1123');
            console.log(this.ruleSettingsData);
            // console.log(!(typeof this.ruleListIndex == 'number'));
            this.m_id = this.ruleSettingsData.m_id


            if (!(typeof this.ruleListIndex == 'number')) {
                return
            }

            // return;

            this.ruleList = this.ruleSettingsData.ruleList[this.ruleListIndex];
            console.log(this.ruleList);
            this.processName = this.ruleList.title;



            this.rules = this.ruleList.rule;
            this.flows = this.ruleList.flow;
            this.copys = this.ruleList.copy;

            for (let i = 0; i < this.rules.length; i++) {
                this.rules[i].depid = this.rules[i].data;
            }
            for (let i = 0; i < this.flows.length; i++) {
                this.flows[i].depid = this.flows[i].userid;
            }
            for (let i = 0; i < this.copys.length; i++) {
                this.copys[i].depid = this.copys[i].userid;
            }

        },
        methods: {
            reviseRule() { //修改可见范围
                this.title = '申请人';
                this.twoTitle = '已选择的部门或成员';

                this.dialogShow = true
                this.isRule = true;
                this.isOne = true;
                this.isFlow = false;
                this.isCopy = false;
                this.isUser = false;
                this.powerList = JSON.parse(JSON.stringify(this.rules))
            },
            reviseFlow() { //修改审批人
                this.title = '审批人';
                this.twoTitle = '已选择的审批人';

                this.dialogShow = true
                this.isRule = false;
                this.isFlow = true;
                this.isCopy = false;
                this.isUser = true;
                this.powerList = JSON.parse(JSON.stringify(this.flows))
            },
            reviseCopy() { //修改抄送人
                this.title = '抄送人';
                this.twoTitle = '已选择的抄送人';

                this.dialogShow = true
                this.isRule = false;
                this.isFlow = false;
                this.isCopy = true;
                this.isUser = true;
                this.powerList = JSON.parse(JSON.stringify(this.copys))
            },
            submitBtn(item) {
                this.dialogShow = false
                console.log('提交按钮接收' + item);
                console.log(item);
                let list = item.powerList;

                if (this.isRule) {
                    this.rules = JSON.parse(JSON.stringify(list))
                } else if (this.isFlow) {
                    this.flows = JSON.parse(JSON.stringify(list))
                } else if (this.isCopy) {
                    this.copys = JSON.parse(JSON.stringify(list))
                }

            },
            resetBtn() {
                this.dialogShow = false
            },
            async reviseBtn() { //修改流程规则
                this.isDel = false;
                console.log(111);
                let rultObj = this.dataHandling()
                if (!rultObj) {
                    return;
                }
                let ruleData = this.ruleSettingsData.ruleList
                let index = this.ruleListIndex;

                for (let i = 0; i < ruleData.length; i++) {
                    if (ruleData[i].title == $.trim(this.processName) && i !== index) {
                        this.$zero.ToastWarning('流程名称重复');
                        return;
                    }
                }
                if (this.m_id) {

                    let res = await this.$api.process.updateRuleTemplate({
                        m_id: this.m_id,
                        r_id: this.ruleList._id,
                        title: $.trim(this.processName),
                        rule: rultObj.rule,
                        flow: rultObj.flow,
                        copy: rultObj.copy
                    })
                    if (res.statusCode === 75200) {
                        // return;
                        this.setRuleSettingsData(this.ruleSettingsData.title, index)
                    } else {
                        return this.$zero.ToastWarning(res.msg)
                    }

                } else {
                    this.ruleSettingsData.ruleListData[index] = {
                        title: $.trim(this.processName),
                        rule: rultObj.rule,
                        flow: rultObj.flow,
                        copy: rultObj.copy
                    }

                    this.setRuleSettingsData(index)
                }
                return;

                this.ruleSettingsData.ruleListData[index] = {
                    title: $.trim(this.processName),
                    rule: rultObj.rule,
                    flow: rultObj.flow,
                    copy: rultObj.copy
                }

                this.setRuleSettingsData(index)

            },
            setRuleSettingsData(title, index) {
                if (typeof index == 'number') {
                    this.ruleSettingsData.ruleList[index] = {
                        title: $.trim(this.processName),
                        rule: this.rules,
                        flow: this.flows,
                        copy: this.copys
                    }
                    this.$zero.ToastSuccess('修改成功');
                    setTimeout(() => {
                        if (this.m_id) {
                            window.location.href = `/procedureEdit/ruleSettings/${title}/${this.m_id}`
                        } else {
                            window.location.href = "/procedureSetRuleSettings";
                        }

                    }, 500)
                } else {
                    this.ruleSettingsData.ruleList.push({
                        title: $.trim(this.processName),
                        rule: this.rules,
                        flow: this.flows,
                        copy: this.copys
                    })
                    this.$zero.ToastSuccess('设置成功');
                    setTimeout(() => {
                        if (this.m_id) {
                            window.location.href = `/procedureEdit/ruleSettings/${title}/${this.m_id}`
                        } else {
                            window.location.href = "/procedureSetRuleSettings";
                        }
                    }, 500)
                }
                this.$zero.setSessionStorage({
                    ruleSettingsData: this.ruleSettingsData
                })

            },
            async preserveBtn() { //添加流程规则
            this.isDel = false;
                let rultObj = this.dataHandling()
                // console.log(rule);
                // console.log(flow);
                // console.log(copy);
                if (!rultObj) {
                    return;
                }
                // console.log(this.m_id);
                let ruleList = this.ruleSettingsData.ruleList
                for (let i = 0; i < ruleList.length; i++) {
                    if (ruleList[i].title == $.trim(this.processName)) {
                        return this.$zero.ToastWarning('流程已存在或名称重复');
                    }
                }
                // console.log(rultObj);
                if (this.m_id) {
                    console.log('进来没');
                    console.log(this.m_id);
                    let res = await this.$api.process.addRuleTemplate({
                        m_id: this.m_id,
                        title: $.trim(this.processName),
                        rule: rultObj.rule,
                        flow: rultObj.flow,
                        copy: rultObj.copy
                    })
                    if (res.statusCode === 75200) {
                        this.setRuleSettingsData(this.ruleSettingsData.title)
                    } else {
                        return this.$zero.ToastWarning(res.msg)
                    }

                } else {
                    this.ruleSettingsData.ruleListData.push({
                        title: $.trim(this.processName),
                        rule: rultObj.rule,
                        flow: rultObj.flow,
                        copy: rultObj.copy
                    })

                    this.setRuleSettingsData();
                }
            },
            returnBtn() {
                this.isDel = false;
                if (this.m_id) {
                    window.location.href = `/procedureEdit/ruleSettings/${this.ruleSettingsData.title}/${this.m_id}`
                } else {
                    window.location.href = "/procedureSetRuleSettings";
                }
            },
            dataHandling() {
                // if()
                // console.log($.trim(this.processName));
                if ($.trim(this.processName) == "") {
                    this.$zero.ToastWarning('请输入流程名称')
                    return;
                } else if (this.rules.length == 0) {
                    this.$zero.ToastWarning('请选择申请人')
                    return;
                } else if (this.flows.length == 0) {
                    this.$zero.ToastWarning('请选择审批人')
                    return;
                }
                let rule = [];
                let flow = [];
                let copy = [];
                for (let i = 0; i < this.rules.length; i++) {
                    rule[i] = {
                        data: this.rules[i].depid,
                        type: this.rules[i].type
                    }
                }
                for (let i = 0; i < this.flows.length; i++) {
                    flow.push(this.flows[i].depid)

                }
                for (let i = 0; i < this.copys.length; i++) {
                    copy.push(this.copys[i].depid)

                }

                return {
                    rule,
                    flow,
                    copy
                }
                // this.$router.push({
                //     path: '/procedureSetUpTwo'
                // })
            }
        },
        beforeDestroy() {
            this.$zero.eventBus.$off("addBookLoadMore");
            this.$zero.eventBus.$off("departmentItem");
            console.log(this.isDel);
            
            if(this.isDel){
                this.$zero.removeSessionStorage('ruleSettingsData')
                this.$zero.removeSessionStorage('ruleTemplate')
            }
        },
        components: {
            MaskLayer
        }
    }
</script>

<style>
    .procedure_approval_condition {
        width: 100%;
        height: 100%;
    }

    .procedure_approval_condition .approval_condition_header {
        position: relative;
        width: 100%;
    }

    .procedure_approval_condition .c_set_up_two_header_btn_one {
        padding: 6px 27px;
        border-radius: 0px;
        position: absolute;
        background-color: #2f78ef;
        border-color: #2f78ef;
        right: 135px;
        top: 12px;
    }

    .procedure_approval_condition .c_set_up_two_header_btn_two {
        padding: 6px 27px;
        border-radius: 0px;
        background-color: #dd4f4f;
        border-color: #dd4f4f;
        position: absolute;
        right: 40px;
        top: 12px;
    }

    .c_approval_condition_box {
        width: 100%;
        height: calc(100% - 51px);
        padding: 20px 40px 40px;
        box-sizing: border-box;
    }

    .c_approval_condition_main {
        width: 100%;
        height: 100%;
        box-sizing: border-box;
        padding: 50px 40px 0px;
        background-color: #fff;
    }

    .c_approval_condition_form {
        padding-bottom: 25px;
        border-bottom: 1px solid #e6e6e6;
    }

    .c_approval_condition_input_box {
        display: flex;
        align-items: center;
        margin-bottom: 15px;
    }

    .procedure_approval_condition .c_title {
        font-size: 15px;
        color: #999;
        width: 110px;
    }

    /* 输入框 */
    .procedure_approval_condition .el-input__inner {
        padding-left: 10px;
        height: 35px;
        line-height: 35px;
        border-radius: none;
        color: #000;
    }

    .procedure_approval_condition .c_input .el-input__inner {
        height: 36px;
        line-height: 36px;
    }

    .procedure_approval_condition .c_input .el-input {
        width: 350px;
    }

    /* 下拉框 */
    .procedure_approval_condition .c_select {
        display: flex;
        align-items: center;
    }

    .procedure_approval_condition .c_select .el-input {
        width: 90px;
        height: 36px;
        display: flex;
        align-items: center;
    }

    .procedure_approval_condition .c_select .el-select .el-input .el-select__caret {
        color: #333;
    }

    .procedure_approval_condition .c_select>span {
        color: #333;
        margin: 0px 15px;
        font-size: 14px;
    }

    .procedure_approval_condition .c_select .c_name {
        background-color: #2f78ef;
        color: #fff;
        /* width: 84px; */
        padding: 0 10px;
        height: 30px;
        line-height: 30px;
        text-align: center;
        font-size: 14px;
        margin-right: 10px;
        display: flex;
        align-items: center;
    }

    .procedure_approval_condition .c_select .c_name>img {
        margin-right: 5px;
    }

    .procedure_approval_condition .c_select .c_btn {
        font-size: 14px;
        color: #2f78ef;
        cursor: pointer;
        /* margin-left: 15px; */
    }

    .ml {
        margin-left: 15px;
    }

    .procedure_approval_condition .c_approval_condition_add {
        padding-top: 40px;
    }

    /* 标题 */
    .procedure_approval_condition .c_approval_condition_title {
        /* color: #333; */
        font-weight: bold;
        font-size: 15px;
    }

    .procedure_approval_condition .c_approval_process_box {
        margin-top: 15px;
    }

    .procedure_approval_condition .c_approval_process_box .c_title {
        padding-top: 15px;
    }

    /* 抄送人和审批人盒子 */
    .procedure_approval_condition .c_approval_process_box {
        display: flex;
    }

    /* 图标盒子 */
    .procedure_approval_condition .c_approval_by_list {
        display: flex;
    }

    /* 头像图片 */
    .procedure_approval_condition .c_approval_by_name>img {
        width: 50px;
        height: 50px;
    }

    .procedure_approval_condition .c_approval_by_name>p {
        font-size: 14px;
        text-align: center;
        /* margin-top: 5px; */
    }

    /* 三点图标 */
    .procedure_approval_condition .c_approval_by_icon {
        margin: 0px 5px;
        font-size: 16px;
        color: #82aef5;
        padding-top: 15px;
    }

    /* 添加图标 */
    .procedure_approval_condition .c_approval_by_add {
        color: #999;
        border: 1px solid #d9d9d9;
        border-radius: 5px;
        width: 50px;
        height: 50px;
        box-sizing: border-box;
        font-size: 26px;
        display: flex;
        justify-content: center;
        align-items: center;
        cursor: pointer;
    }
</style>